.chat{
    flex: 0.65;
    display: flex;
    flex-direction: column;
}

.chat-header{
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat-header-info{
    flex: 1;
    padding-left: 2%;
}

.chat-header-info h3 {
    margin-bottom: 3px;
    font-size: small;
    font-weight: 700;
}

.chat-header-info > p {
    color:gray;
}

.chat-header-right{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat-body{
    flex: 1;
    background-image: url('bg.png');
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y:scroll;
    scrollbar-width: none;
}
.message{
    position: relative;
    font-size: 16px;
    border-radius: 10px;
    padding: 10px;
    background-color: white;
    width:fit-content;
}

.sender-name{
    position: absolute;
    top:-15px;
    left: 17px;
    font-weight: 730;
    font-size: xx-small;
}
.timestamp{
    margin-left: 10px;
    margin-bottom: 15px;
    font-size: xx-small;
}
.reciever{
    margin-left: auto;
    margin-bottom: 15px;
    background-color: #dcf8c6;
}

.chat-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 62px;
    border: 1px solid lightgray;
}
.chat-footer > form{
    flex: 1;
    display: flex;
}

.chat-footer > form > input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    margin: 0 10px 0px 10px;
}

.chat-footer > form > button{
    display: none;
}